import React from "react";
import { Dialog, DialogContent, AppBar, Toolbar, Button, Typography, IconButton } from "@material-ui/core";
import Slide from '@material-ui/core/Slide';
import { Close } from "@material-ui/icons";

const Transition: any = React.forwardRef(function (props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
} as any);

export default function Modal(props: {
    show: boolean,
    onClose: (event: {}, reason?: "backdropClick" | "escapeKeyDown") => void,
    fullScreen: boolean,
    title: string,
    closeButtonText?: string | "ICON",
    children?: any
}) {
    const { show, onClose, fullScreen, title } = props;
    var closeButtonText = props.closeButtonText || "ICON";
    return (<Dialog open={show} onClose={onClose} fullScreen={fullScreen} TransitionComponent={Transition}>
        {fullScreen && (
            <AppBar position="relative">
                <Toolbar>
                    <Typography color="inherit" variant="h4" style={{ flexGrow: 1 }}>{title}</Typography>
                    {closeButtonText === "ICON"
                        ? (<IconButton onClick={onClose} type="button" color="inherit">
                            <Close />
                        </IconButton>)
                        : (<Button type="button" onClick={onClose} color="inherit">
                            {closeButtonText || "SALIR"}
                        </Button>)
                    }
                </Toolbar>
            </AppBar>)}
        <DialogContent>
            {props.children}
        </DialogContent>
    </Dialog>)
}