import React from "react";
import { Grid, Card, CardContent, CardActions, CardMedia, Typography, IconButton, Input, InputAdornment } from "@material-ui/core";
import { Add, Remove, AttachMoney } from "@material-ui/icons";

export default function ProductGrid(props: {
    onActionClick: any;
    items: any[];
    visible: boolean;
    productoSelecciones?: { [idProducto: number]: number[][] };
    editablePrices: boolean;
    onPriceChange: (event: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => void;
}) {
    const visible_style: any = {}
    if (!props.visible) visible_style.display = "none";
    return (<Grid
        container
        alignItems="center"
        style={{ flex: 1, /*overflow: "auto",*/ marginTop: 5, ...visible_style }}
        spacing={3}>
        {props.items.map((item: any) => {
            return <ProductItem
                key={item.id}
                {...item}
                editablePrice={props.editablePrices}
                onPriceChange={props.onPriceChange}
                onActionClick={props.onActionClick}
            />
        })}
    </Grid>)
}

class ProductItem extends React.PureComponent<any, any> {
    render() {
        const props = this.props;
        const item = props;
        return (<Grid item sm={6} md={4} xs={12}>
            <Card>
                <CardMedia component="img" style={{ height: 180, objectFit: "contain" }} image={item.imgSrc || "https://diegopq95.github.io/take-orders/img/default.png"} />
                <CardContent style={{ display: "flex", flexDirection: "row" }}>
                    <Typography style={{ flex: 1, height: 60, overflow: "hidden" }} variant="subtitle1">{item.nombre}</Typography>

                    {props.editablePrice
                        ? <Input
                            style={{ width: 100 }}
                            type="number"
                            onChange={props.onPriceChange}
                            value={"" + item.pvp_final}
                            name={"" + item.id}
                            startAdornment={<InputAdornment position="start" >
                                <AttachMoney />
                            </InputAdornment>}
                        />
                        : <Typography variant="h6">$ {item.pvp_final.toFixed(2)}</Typography>}

                </CardContent>
                <CardActions>
                    <IconButton onClick={props.onActionClick} name="+" value={item.id}>
                        <Add />
                    </IconButton>
                    <Typography variant="body1">{item.cantidad}</Typography>
                    <IconButton disabled={!(item.cantidad > 0)} onClick={props.onActionClick} name="-" value={item.id}>
                        <Remove />
                    </IconButton>
                </CardActions>
            </Card>
        </Grid>);
    }
}